.plot-container {
    width: 100%;
    overflow: auto;
    padding: 10px;
}

.plot-contents-container {
    display: flex;
    align-items: center;
    column-gap: 30px;
    width: calc(109% - 1px);

}

.left-main-index {
    margin-top: auto;
    margin-bottom: auto;
    writing-mode: vertical-lr;
    font-weight: 700;
    transform: rotate(-180deg);
}

.main-plot-column-container {
    padding-left: 10px;
    list-style-type: none;
    margin-left: 85px;
}

.main-plot-column-name {
    margin-bottom: 30px;
    text-align: center;
    font-weight: 700;
}

.main-plot-column-index-container {
    display: flex;
    width: 100%;
    justify-content: flex-end;
}

.main-plot-column-index {
    width: 50px;
    text-align: center;
}

.main-plot-first-category-container {
    display: flex;
}

.main-plot-first-category-index {
    writing-mode: vertical-lr;
    transform: rotate(-180deg);
    margin: auto;
    width: fit-content;
    white-space: nowrap;
    font-weight: 600;
}

.main-plot-first-category-contents-container {
    padding-left: 10px;
    list-style-type: none;
}

.main-plot-level-data-container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

.main-plot-level {
    margin-right: 30px;
    white-space: nowrap;
    text-align: center;
    line-height: 40px;
}

.main-plot-data {
    display: flex;
    align-items: center;
    width: 100%;
}

.main-plot-second-category-container {
    display: flex;
}

.main-plot-second-category-index {
    writing-mode: vertical-lr;
    transform: rotate(-180deg);
    margin: auto;
    width: fit-content;
    white-space: nowrap;
    font-weight: 600;
}

.main-plot-second-category-contents-container {
    padding-left: 10px;
    list-style-type: none;
    margin-top: 30px;
}

.legends-container {
    display: flex;
    column-gap: 10px;
    list-style-type: none;
    padding-left: 0px;
    margin-top: 90px;
}

.legends-content {
    display: flex;
    align-items: center;
}